<template>
    <div class="w1180" style="height: 50px;overflow: hidden; background-color: #fff;">
        <table width="100%" border="0" cellspacing="0" cellpadding="0">
            <tbody>
            <tr>
                <td width="61%" height="50">
                        <span class="w1180"
                              style="height: 50px; line-height: 50px; background-color: #fff;padding-left: 50px">
                           <a-icon type="home" @click="goBack"/> 欢迎来到智慧党建网站！
                        </span>
                </td>
                <td width="20%" height="50">
                    <div class="search" style="display: none">
                        <input type="text" class="ipt-sea" placeholder="请输入您的搜索关键词">
                        <a style="margin-top: -30px;" href="javascript:;">&nbsp;</a>
                    </div>
                </td>
                <td width="8%" height="50">
                    <a style="width: 90px; height: 30px; display: block;margin-right: 10px"
                       @click="jump('login')">
                        <img :src="require('@/assets/pc/images/logon_ann.png')" width="90" height="30" alt=""/>
                    </a>
                </td>
                <td width="11%" height="50">
                    <a style="width: 130px; height: 30px; display: block;"
                       @click="jump('apply')">
                        <img :src="require('@/assets/pc/images/sqrd_ann.png')" width="130" height="30" alt=""/>
                    </a>
                </td>
            </tr>
            </tbody>
        </table>

    </div>
</template>

<script>
    export default {
        name: "Head",
        methods: {
            goBack() {
                this.$router.push({name: 'root'})
            },
            jump(type) {
                let _self = this;
                switch (type) {
                    case 'login':
                        this.$router.push({name: 'login'});
                        break;
                    case 'apply':
                        this.$confirm({
                            title: '操作确认',
                            content: '请问您是第一次申请吗？',
                            okText: '是的，我是第一次申请',
                            cancelText: '不，我有申请码',
                            onOk() {
                                _self.$router.push({name: '申请入党', params: {activist_code: 0, step: 0}});
                            },
                            onCancel() {
                                // this.$router.push({name: '申请入党'});
                            },
                        });
                        break;
                }
            }
        },
    }
</script>

<style scoped>
    a {
        cursor: pointer;
    }
</style>